import {BrowserModule} from '@angular/platform-browser';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {MATERIAL_COMPATIBILITY_MODE} from '@angular/material';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {NgModule} from '@angular/core';
import {MaterialModule} from './modules/material.module';
import {HttpClientModule} from '@angular/common/http';

// Modules
import {VisitorModule} from './modules/visitor.module';
import {AppComponent} from './app.component';
// Components
import {MainNavbarComponent} from './components/shared/navbar/navbar.component';
// Services
import {AuthService} from './network/services/auth.service';

@NgModule({
  declarations: [
    AppComponent,
    MainNavbarComponent
  ],
  imports: [
    BrowserModule,
    MaterialModule,
    HttpClientModule,
    BrowserAnimationsModule,
    FormsModule,
    ReactiveFormsModule,
    VisitorModule
  ],
  providers: [
    AuthService,
    {
      provide: MATERIAL_COMPATIBILITY_MODE,
      useValue: true
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



